/* 
====== 
Variables 
======
*/
:root {
  --primaryLightColor: #d4e6a5;
  --primaryColor: #68b92e;
  --primaryDarkColor: #d45a38;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBackground: #f1f5f8;
  --mainOverlay: rgba(58, 56, 58, 0.4);
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #8b8a8a;
  --mainRed: #bd0303;
  --mainTransition: all 0.2s linear;
  --mainSpacing: 0.3rem;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --mainBorderRadius: 0.25rem;
  --smallWidth: 85vw;
  --maxWidth: 40rem;
  --fullWidth: 1170px;
}
/* 
====== 
Global Styles 
======
*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: var(--mainBlack);
  background: var(--mainBackground);
  line-height: 1.4;
  font-size: 1rem;
  font-weight: 300;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--slantedFont);
  margin-bottom: 1.25rem;
  letter-spacing: var(--mainSpacing);
}
p {
  margin-bottom: 1.25rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: var(--mainBlack);
}
img {
  width: 100%;
  display: block;
}
.container {
  background-color: var(--mainBlack);
  width: 20vw;
  height: 70vh;
  overflow: hidden;
  border-radius: 1rem;
  text-align: center;
  margin-inline: auto;
  margin-top: 5rem;
}
.display {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 25%;
  text-align: right;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  background-color: var(--mainBlack);
  color: var(--mainWhite);
  padding: 0.5rem;
}
.output {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.65rem;
  color: rgb(119, 117, 119);
  font-weight: 500;
}
.input {
  font-size: 2rem;
}
.btn {
  background-color: var(--mainOverlay);
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  border: none;
  font-size: 1.5rem;
  color: var(--mainWhite);
}
.btn:active {
  background-color: var(--darkGrey);
  transition: var(--mainTransition);
  opacity: 0.5;
  transition-duration: 0.3s;
  font-size: 1.25rem;
  color: rgb(255, 255, 255);
}

#equals {
  background-color: var(--primaryColor);
}
#divide,
#multiply,
#subtract,
#add,
#questent,
#plus-minus,
#bracket {
  color: var(--primaryColor);
}
#clear {
  color: var(--primaryDarkColor);
}
.btn-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1px;
  justify-content: center;
}
#equals {
  grid-column-start: 4;
  grid-row-start: 5;
}
#clear {
  grid-column-start: 1;
  grid-row-start: 1;
}
#one {
  grid-column-start: 1;
  grid-row-start: 2;
}

#two {
  grid-column-start: 2;
  grid-row-start: 2;
}
#three {
  grid-column-start: 3;
  grid-row-start: 2;
}
#four {
  grid-column-start: 1;
  grid-row-start: 3;
}
#five {
  grid-column-start: 2;
  grid-row-start: 3;
}
#six {
  grid-column-start: 3;
  grid-row-start: 3;
}
#seven {
  grid-column-start: 1;
  grid-row-start: 4;
}
#eight {
  grid-column-start: 2;
  grid-row-start: 4;
}
#nine {
  grid-column-start: 3;
  grid-row-start: 4;
}
#zero {
  grid-column-start: 2;
  grid-row-start: 5;
}
#decimal {
  grid-column-start: 3;
  grid-row-start: 5;
}
#add {
  grid-column-start: 4;
  grid-row-start: 4;
}
#subtract {
  grid-column-start: 4;
  grid-row-start: 3;
}
#multiply {
  grid-column-start: 4;
  grid-row-start: 2;
}
#divide {
  grid-column-start: 4;
  grid-row-start: 1;
}
#plus-minus {
  grid-column-start: 1;
  grid-row-start: 5;
}
@media screen and (max-width: 800px) {
  .container {
    min-width: 35vw;
    height: 55vh;
    text-align: center;
  }
  .btn {
    width: 3rem;
    height: 3rem;
    border: none;
    font-size: 0.75rem;
  }
}
